@import "@/assets/styles/_variables.scss";

    .char {
        padding-top: 0;
        padding-bottom: 40px;

        &__nav {
            @include content;

            display: flex;
            align-items: center;
            justify-content: center;
            width: 100%;
            background-color: $white;
            z-index: 100;
            position: fixed;
            left: 0;
            bottom: 0;
            box-shadow: 0 0 12px transparentize($black, .75);

            &-item {
                @include css_anim($item: color);

                color: $black;
                text-decoration: none;
                cursor: pointer;
                font-size: 16px;
                font-weight: 500;
                padding: 12px 16px;
                display: inline-flex;
                border-left: 1px solid $gray;

                &:last-child {
                    border-right: 1px solid $gray;
                }

                &.active {
                    color: $green;
                    cursor: default;
                }

                &:hover {
                    &:not(.active) {
                        color: $gray;
                    }
                }
            }
        }

        &__block {
            @include content;

            padding-top: 8px;
            padding-bottom: 8px;

            &.fixed {
                position: sticky;
                top: 40px;
                background-color: $white;
                z-index: 100;
                border-bottom: 1px solid $gray;
            }
        }

        &__row {
            display: flex;
            flex-wrap: wrap;
            margin-bottom: 8px;

            & > * {
                margin-right: 8px;

                &:last-child {
                    margin-right: 0;
                }
            }

            &.stats {
                justify-content: flex-start;
                margin: 8px auto 0;
            }

            &.align {
                margin: 0;

                & > * {
                    margin: 0 auto 0 0;
                }
            }

            &.base {
                justify-content: space-between;

                > * {
                    margin-right: 0;

                    &:nth-child(n + 4) {
                        margin-bottom: 0;
                    }
                }
            }
        }

        &__name {
            margin-right: auto;
            font-weight: 600;
        }

        &__background {
            margin-left: auto;
        }

        &__lvl {
            &-value {
                font-weight: 600;
            }
        }

        &__exp {
            &-value {
                font-weight: 600;
            }
        }
    }
